export default {
	data() {
		return {
			tabs: ['详情', '目录', '评价'],
			currentTabIndex: 0,
			swiperHeight: 0,
			scrollTop: 0,
			top: 0,
			isFixed: false
		}
	},
	watch: {
		scrollTop(newval,oldval) {
			this.updateStickyState()
		}
	},
	onPageScroll({scrollTop}) {
		this.scrollTop = scrollTop
	},
	onReady() {
		this.getStickyTop()
	},
	mounted() {
		this.setSwiperHeight()
	},
	methods: {
		switchTab(index) {
			if(this.currentTabIndex !== index) {
				this.currentTabIndex = index
			}
		},
		switchSwiper(e) {
			this.currentTabIndex = e.detail.current
			this.setSwiperHeight()
		},
		setSwiperHeight() {
			let query = uni.createSelectorQuery().in(this)
			query.selectAll('.swiper_item').boundingClientRect( res => {
				this.swiperHeight = res[this.currentTabIndex].height
			}).exec()
		},
		updateStickyState() {
			this.isFixed = this.scrollTop >= this.top ? true : false
		},
		getStickyTop() {
			let query = uni.createSelectorQuery().in(this)
			query.select('.tab_wrapper').boundingClientRect( res => {
				this.top = res.top
			}).exec()
		}
	}
}
